#app {
  color: $textColorMain;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-style: normal;
  @include font-smoothing();
  display: grid;
  grid-template-columns: 1fr;
  height: 100vh;

  .main-content {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch; /* Lets it scroll lazy */
    padding: $lg $md;
  }

  &.appcustomer,
  &.apppartner {
    grid-template-rows: $titleBarHeight auto;

    .title-bar{
      grid-row-start: 1;
      grid-row-end: 2;
    }

    .main-content {
      grid-row-start: 2;
      height: calc(100vh - #{$titleBarHeight});
      padding: $lg $md;
    }
  }

  &.appem {
    grid-template-rows: $titleBarHeight $navbarHeight auto;

    .title-bar{
      grid-row-start: 1;
      grid-row-end: 2;
    }

    .pmr-navbar {
      grid-row-start: 2;
      grid-row-end: 3;
    }

    .main-content {
      grid-row-start: 3;
      height: calc(100vh - (#{$titleBarHeight} + #{$navbarHeight}));
      padding: $lg $md;
    }
  }
}

.title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: $titleBarHeight;
  padding: 0 $md;
  background: $white;
  z-index: $zTitleBar;
  box-shadow: 0px 2px 2px 2px rgba($grayDark, .15);
  grid-row-start: 1;
  grid-row-end: 2;

  .app-title {
    color: $textColorMain;
    text-decoration: none;
  }

  .time-logo {
    margin-right: $sm;
  }

  .wwt-logo,
  .time-logo {
    height: 29px;
  }

  .wwt-logo,
  .time-logo,
  .app-name {
    vertical-align: middle;
  }

  .app-name {
    color: $grayMid;
    font-size: 18px;
  }

  nav {
    display: flex;
  }

  .nav-tab {
    border: 2px solid transparent;
    font-size: 1rem;
    outline: none;
    cursor: pointer;
    color: $grayMid;
    padding: $sm 0;
    transition: 300ms all ease;
    position: relative;
    margin: 0 $sm;
    background: transparent;

    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 0%;
      height: 2px;
      transition: 300ms all ease;
    }

    &:hover,
    &:focus,
    &.active{
      color: $wwtBlue;

      &:after {
        background-color: $wwtBlue;
        width: 100%;
      }
    }
  }
}
