@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}








































.hours-tab-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: .5rem;
  border-bottom: 1px solid $grayVeryLight;
  padding-left: $md;
  padding-right: $md;

  button {
    align-self: flex-end;
  }
}

.week-selector {
  font-weight: 800;
  font-size: 1rem;
  display: flex;
  align-items: center;

  &-dates {
    margin: 0 $md;
  }
}

.arrow-btn {
  background: $white;
  border: 1px solid $blue;
  cursor: pointer;
  outline: none;
  padding: $xs/4 $xs;
  box-shadow: 1px 1px 2px 2px rgba($grayDark, .15);

  &:hover,
  &:focus {
    background: $blue40;
    border-color: $wwtBlue;
  }

  &:active {
    background: $wwtBlue;

    svg {
      fill: $white;
    }
  }

  &:disabled,
  .disabled {
    cursor: auto;
    border: 1px solid $grayLight;
    background: $graySuperLight;
    box-shadow: none;

    &:hover,
    &:focus,
    &:active {
      background: $graySuperLight;
    }

    svg {
      fill: $grayLight;
    }
  }

  svg {
    fill: $blue;
  }
}
