@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}









































h2 {
  margin-top: 0;
}

.card {
  background: $white;
  max-width: 80rem;
  width: 100%;
  box-shadow: 0px 2px 2px 2px rgba($grayDark, .15);
  padding: $lg;
  margin-bottom: $lg;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $md;

  h2 {
    margin: 0;
  }
}

.project-grid {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;

  &-header,
  &-row {
    display: grid;
    grid-template-columns: 4fr 1fr;
    grid-template-rows: auto ;
  }

  &-header {
    border-bottom: 1px solid $grayVeryLight;
    text-transform: uppercase;
    letter-spacing: .05em;
    padding: $sm 0;
  }

  .project-id {
    font-weight: 800;
    margin-right: .25rem;
  }

  &-row {
    padding: $md 0;
    border-bottom: 1px solid $graySuperLight;
    align-items: center;

    &:last-child {
      border: none;
    }
  }
}

.project-id,
.project-title {
  font-size: 1rem;
}
