@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}





















































h3{
  margin-bottom: 0;
}
h1 {
  margin-top: 0;
  margin-bottom: $lg;
}
.card {
  background: $white;
  max-width: 80rem;
  width: 100%;
  box-shadow: 0px 2px 2px 2px rgba($grayDark, .15);

  & + .card {
    margin-top: $xl;
  }
}

.card-header{
  padding: $lg;
}

.card-body {
  padding: $lg $md
}

.card-header {
  border-bottom: 1px solid $grayVeryLight;
  padding-bottom: 0;
  padding-top: $md;
  background: $blue10;
  color: $white;
}

.tab-group {
  display: flex;
}

.tab {
  color: $white;
  border: 1px solid transparent;
  display: inline-block;
  position: relative;
  border-radius: 0;
  padding: $sm $lg;
  font-size: .875rem;
  line-height: $inputLineHeight;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
  transition: all ease 300ms;
  top: 1px;

  &:hover,
  &:focus {
    background: rgba($white, .15);
    outline: none;
  }

  &.active {
    color: $blue;
    background: $white;
    border-top: 1px solid $grayVeryLight;
    border-right: 1px solid $grayVeryLight;
    border-bottom: 1px solid $white;
    border-left: 1px solid $grayVeryLight;

    &:hover,
    &:focus {
      color: $wwtBlue;
    }
  }
}
