@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

















*,
*:after,
*:before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    transition: 0.25s ease-in-out;
    outline: none;
}
.toggle {
    display: inline-block;
    width: 60px;
    margin: 0 auto;
    text-align: center;
}
.ios-toggle,
.ios-toggle:active {
    position: absolute;
    top: -5000px;
    height: 0;
    width: 0;
    opacity: 0;
    border: none;
    outline: none;
}
.checkbox-label {
    display: block;
    position: relative;
    padding: 10px;
    font-size: 12px;
    line-height: 16px;
    width: 100%;
    height: 36px;
    border-radius: 18px;
    background: #f8f8f8;
    cursor: pointer;

    &:before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        line-height: 34px;
        text-indent: 40px;
        height: 36px;
        width: 36px;
        border-radius: 100%;
        top: 0;
        left: 0;
        right: auto;
        background: white;
        box-shadow: 0 3px 3px rgba(0,0,0,.2),0 0 0 2px #dddddd;
    }

    &:after {
        // content: attr(data-off);
        display: block;
        position: absolute;
        z-index: 0;
        top: 0;
        left: -300px;
        padding: 10px;
        height: 100%;
        width: 300px;
        text-align: right;
        color: #bfbfbf;
        white-space: nowrap;
    }
}

.ios-toggle {
  & + .checkbox-label {
      box-shadow: inset 0 0 0 0 $blue,0 0 0 2px $grayVeryLight;
  }

  &:focus + .checkbox-label {
    box-shadow: inset 0 0 0 0 $wwtBlue,0 0 0 2px $wwtBlue;

    &:before {
        box-shadow: 0 0 0 2px transparent,0 3px 3px $wwtBlue;
    }
  }
}

.ios-toggle:checked {
  & + .checkbox-label {
      box-shadow: inset 0 0 0 20px $blue,0 0 0 2px $blue;
  }

  & + .checkbox-label:before {
      left: calc(100% - 36px);
      box-shadow: 0 0 0 2px transparent,0 3px 3px rgba(0,0,0,.3);
  }

  & + .checkbox-label:after {
      // content: attr(data-on);
      left: 60px;
      width: 36px;
  }

  &:focus + .checkbox-label {
    box-shadow: inset 0 0 0 20px $wwtBlue,0 0 0 2px $wwtBlue;
  }
}

