@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

































































.hours-grid {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: auto;
  text-align: left;
  padding-top: $md;
}

.hours-grid-header {
  grid-row-start: 1;
  border-bottom: 1px solid $graySuperLight;
}

.hours-grid-body {
  grid-row-start: 2
}

.resource-hour,
.hours-grid-header-col.day-of-week,
.resource-total,
.hours-grid-header-col.total {
  border-left: 1px solid $graySuperLight;
  text-align: center;
}

.hours-grid-row,
.hours-grid-header {
  width: 100%;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 2fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1fr 1.5fr;
  padding: 0;
  align-items: center;
}

.hours-grid-header {
  align-items: flex-end;
  font-weight: 700;
}

.hours-grid-row {
  border-bottom: 1px solid $graySuperLight;

  &:nth-child(even){
    background: rgba($graySuperLight, .5);
  }

  &:last-child {
    border-bottom: none;
  }
}

.hours-grid-header-col,
.hours-grid-col {
  display: inline-block;
  padding: $sm;
}

.hours-grid-header-col {
  display: flex;
  flex-direction: column;
}

.hours-grid-col {
  padding-top: $lg;
  padding-bottom: $lg;
}

.name,
.resource-name {
  padding-left: $md;
}

.resource-approval {
  button {
    margin: $xxs;
  }

  .approved {
    color: $greenDark;
  }

  .rejected {
    color: $redDark;
  }
}

@media (max-width: 1000px) {
  .hours-grid-row,
  .hours-grid-header {
    grid-template-columns: 1fr 2fr 1fr 2fr;
  }

  .day-of-week,
  .resource-hour {
    display: none;
  }

  .resource-total,
  .hours-grid-header-col.total {
    border-left: none;
  }
}

