@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}



































.hours-tab-header {
  display: flex;
  justify-content: flex-end;
  padding-left: $md;
  padding-right: $md;
}

.burn-rate-list {
  margin: 0;
  padding: 0;
  list-style: none;

  &:last-child {
    .burn-rate-row:last-child {
      border-bottom: none;
    }
  }
}

.burn-rate-row {
  display: flex;
  padding: $lg $md;
  border-bottom: 1px solid $graySuperLight;
  align-items: center;

  &:nth-child(even){
    background: rgba($graySuperLight, .5);
  }

  .name{
    flex: 0 0 150px;
    text-align: left;
  }

  .progress-bar {
    flex: 1 1 auto;
  }
}
